<template>
  <div class="collection">
    <v-header title="精品推荐" :headerLeftStatus="headerLeftStatus" />
    <div class="item">
      <div
        class="list"
        v-for="(list, index) in collections"
        :key="index"
        @click="goDetail(list)"
      >
        <div class="list-shop">
          <div class="list-shop-box">
            <div class="list-box-left">
              <img :src="JSON.parse(list.post.data).img_url" />
              <p>
                <span class="title">{{ list.user.username }}</span>
                <span class="info">{{ list.post.post }}</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import header from "@/components/header/index";
import { getRecomend } from "@/api/data";
export default {
  data() {
    return {
      headerLeftStatus: true,
      collections: []
    };
  },
  methods: {
    goDetail(data) {
      this.$router.push({
        name: "recommendDetail",
        params: data
      });
    }
  },
  mounted() {
    getRecomend().then(r => {
      console.log(
        "%c [r]: ",
        "color: #bf2c9f; background: pink; font-size: 13px;",
        r
      );
      this.collections = r;
    });
  },
  computed: {},
  components: {
    "v-header": header
  }
};
</script>

<style lang="less" scoped>
.collection {
  width: 100%;
  height: auto;
  .item {
    width: 100%;
    height: 100%;
    position: relative;
    top: 1.5rem;
    .list {
      width: 100%;
      background: #ffffff;
      display: flex;
      flex-direction: column;
      margin-bottom: 0.05rem;
      .list-title {
        width: 100%;
        height: 1.3rem;
        border-bottom: 1px solid #f4f4f4;
        font-size: 0.35rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .inventory {
          color: #55b451;
          padding-left: 0.5rem;
          font-weight: 500;
        }
        .noCollection {
          padding-right: 0.5rem;
          color: #626262;
          font-weight: 500;
        }
      }
      .list-shop {
        width: 100%;
        height: 3.4rem;
        .list-shop-box {
          width: 95%;
          height: 100%;
          margin: auto;
          display: flex;
          .list-box-left {
            height: 100%;
            position: relative;
            display: flex;
            img {
              width: 2.9rem;
              height: 2.7rem;
              margin-top: 0.2rem;
            }
            p {
              height: 100%;
              font-size: 0.45rem;
              display: flex;
              flex-direction: column;
              text-indent: 0.3rem;
              margin-left: 10px;
              .title {
                width: 4rem;
                color: #514e56;
                font-weight: 500;
                margin-top: 0.3rem;
                margin-bottom: 0.2rem;
              }
              .info {
                color: #888;
                font-weight: 500;
                word-break: break-all;
                -webkit-line-clamp: 3;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
          }
          .list-box-right {
            width: 32%;
            height: 100%;
            display: flex;
            align-items: flex-end;
            position: relative;
            p {
              width: 3rem;
              height: 0.95rem;
              line-height: 0.95rem;
              background: #ff0202;
              border-radius: 0.5rem;
              color: #fff;
              font-size: 0.2rem;
              text-align: center;
              position: absolute;
              bottom: 0.5rem;
              font-size: 0.35rem;
            }
          }
        }
      }
    }
  }
}
</style>



