<template>
  <div class="goodDetail" id="transitionName">
    <v-header title="推荐页面" :headerLeftStatus="headerLeftStatus" />
    <div style="padding-top: 90px;">
      <transition name="fade">
        <div class="good-imgae">
          <img
            style="width: 100%;"
            :src="goodData.img_url"
            :alt="goodData.name"
          />
        </div>
      </transition>

      <h4 class="title">{{ goodData.name }}</h4>

      <div class="good-text">
        <textarea
          placeholder="请输入介绍文案"
          class="text"
          name="introduction"
          v-model="introduction"
          rows="10"
        ></textarea>
      </div>
      <div class="btn">
        <mt-button style="width: 100%;" type="danger" @click="goPost"
          >立即推荐</mt-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from "mint-ui";
import { Toast } from "mint-ui";
import { postShare } from "@/api/data";
import header from "@/components/header/index";
export default {
  name: "share",
  data() {
    return {
      goodData: {},
      introduction: "",
      headerLeftStatus: true
    };
  },
  methods: {
    goPost() {
      postShare({
        userId: localStorage.getItem("token"),
        post: this.introduction,
        data: this.goodData
      }).then(r => {
        console.log(
          "%c [ r ]: ",
          "color: #bf2c9f; background: pink; font-size: 13px;",
          r
        );
        Toast({
          message: "提交成功",
          duration: 3000
        });
        return false;
      });
    }
  },
  mounted() {
    console.log("[ this.$route ] >", this.$route);
    this.goodData = this.$route.params;
  },
  components: {
    "v-header": header
  }
};
</script>

<style lang="less" scoped>
.title {
  margin: 20px 0;
  text-align: center;
  font-size: 16px;
}
.good-imgae {
  width: 70%;
  height: 7rem;
  display: block;
  margin: auto;
}

.good-text {
  width: 80%;
  display: block;
  margin: 30px auto 0;
}
.text {
  width: 100%;
  font-size: 12px;
}
.btn {
  width: 80%;
  margin: 30px auto 0;
}
</style>



