import { UP_CNUM } from "../mutations-type";

import { Toast, MessageBox, Indicator } from "mint-ui";

const state = {
  cnum: 0
};

const mutations = {
  // 修改评论数
  [UP_CNUM](state, data) {
    state.cnum = data;
  }
};

export default {
  namespaced: true, //开启命名
  state,
  mutations
};



