<template>
  <div>
    <v-header title="评论列表" :headerLeftStatus="headerLeftStatus" />
    <div class="posbox">
      <ul>
        <li class="clist" v-for="(item, index) in comments" :key="index">
          <p class="name">{{ item.user.username }}</p>
          <p class="comment">{{ item.comment.content }}</p>
        </li>
      </ul>

      <div class="good-text">
        <textarea
          placeholder="请输入新的评论"
          class="text"
          name="comment"
          v-model="newcomment"
          rows="10"
        />

        <div class="btn">
          <mt-button style="width: 100%;" @click="postComment"
            >提交评论</mt-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import header from "@/components/header/index";
import { Toast } from "mint-ui";
import { getComments, addComment } from "@/api/data";
export default {
  data() {
    return {
      headerLeftStatus: true,
      postId: "",
      comments: [],
      newcomment: ""
    };
  },
  methods: {
    // 获取评论列表
    getComment() {
      getComments({
        params: {
          postId: this.postId
        }
      }).then(r => {
        console.log(
          "%c [r]: ",
          "color: #bf2c9f; background: pink; font-size: 13px;",
          r
        );
        this.comments = r;
        this.$store.commit("cnum/UP_CNUM", r.length || 0);
      });
    },
    // 添加评论
    postComment() {
      addComment({
        userId: localStorage.getItem("token"),
        postId: this.postId,
        content: this.newcomment
      }).then(r => {
        console.log(
          "%c [ r ]: ",
          "color: #bf2c9f; background: pink; font-size: 13px;",
          r
        );
        Toast({
          message: "提交成功",
          duration: 1000
        });
        this.newcomment = "";
        this.getComment();
        return false;
      });
    }
  },
  mounted() {
    this.postId = JSON.parse(this.$route.params.id);
    this.getComment();
  },
  computed: {},
  components: {
    "v-header": header
  }
};
</script>

<style lang="less" scoped>
.posbox {
  position: relative;
  top: 1.7rem;
}
.clist {
  margin: 0 10px 10px 10px;
  padding: 20px;
  font-size: 14px;
  border: 1px solid #616161;
  border-radius: 5px;
  background: #fff;
  .name {
    padding-bottom: 10px;
    border-bottom: 1px dashed #133136;
  }
  .comment {
    padding-top: 10px;
  }
}
.good-text {
  width: 80%;
  display: block;
  padding-top: 30px;
  margin: auto;
  .text {
    width: 100%;
    font-size: 12px;
  }
  .btn {
    width: 100%;
    margin: 20px auto 0;
  }
}
</style>



