import http from "./http";

// export function getData() {
//     return http.axios_get('/static/data.json') //模拟接口
// }

export function getData() {
  return http.axios_get("/api/data"); //后台接口
}

export function getHomeData() {
  return http.axios_get("/static/data.json"); //首页接口
}

//注册
export function register(parms) {
  return http.axios_post("/api/register", parms);
}

//登录
export function login(parms) {
  return http.axios_post("/api/login", parms);
}

//推荐
export function postShare(parms) {
  return http.axios_post("/api/addPost", parms);
}

//获取推荐
export function getRecomend(parms) {
  return http.axios_post("/api/getPost", parms);
}

//获取评论
export function getComments(parms) {
  return http.axios_get("/api/getComments", parms);
}
//提交评论
export function addComment(parms) {
  return http.axios_post("/api/addComment", parms);
}



