<template>
  <div class="goodDetail" id="transitionName">
    <v-header title="推荐详情" :headerLeftStatus="headerLeftStatus" />

    <div class="posbox">
      <div class="user">
        <div class="img">
          <img style="width: 100%;" :src="imgs[imgIndex]" />
        </div>

        <div class="txt">
          <p class="name">{{ username }}</p>
          <p class="info">
            {{ introduction }}
          </p>
        </div>
      </div>

      <div class="goodDetaiSwipe">
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="(v, index) in goodData.swiper" :key="index">
            <img :src="v.swipe" alt="图片" />
          </mt-swipe-item>
        </mt-swipe>
      </div>

      <div class="goodDetailBox">
        <mt-navbar v-model="selected">
          <mt-tab-item id="tab-container1">图文详情</mt-tab-item>
          <mt-tab-item id="tab-container2">参数</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected" swipeable>
          <mt-tab-container-item id="tab-container1">
            <div class="goodDetailImg">
              <p v-for="(ov, index) in goodData.Images" :key="index">
                <img v-bind:src="ov.one" alt="详情图片" />
              </p>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="tab-container2">
            <div class="peizhi" v-html="goodData.homePeizhi"></div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>

      <div class="footer">
        <div class="num">
          评论数 <mt-badge type="error">{{ cnum }}</mt-badge>
        </div>
        <div class="btn" @click="goComment()">
          点击查看评论详情
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import header from "@/components/header/index";
import { getComments } from "@/api/data";
import { Badge } from "mint-ui";
export default {
  name: "share",
  data() {
    return {
      goodData: {},
      introduction: "",
      username: "",
      headerLeftStatus: true,
      selected: "tab-container1",
      cNum: 0,
      imgs: [
        "https://img1.baidu.com/it/u=898692534,2766260827&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1687453200&t=e7e8c6353fdf702874484fad5f4e04e0",
        "https://img0.baidu.com/it/u=559033494,4222560847&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
        "https://img2.baidu.com/it/u=170237391,555920326&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1687453200&t=f433d051ea004113594424e4468a5ae3",
        "https://img1.baidu.com/it/u=1889141371,3742825600&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1687453200&t=0f0835f96ad29092c7386d7dacf760b7",
        "https://img0.baidu.com/it/u=779040798,3900256075&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1687453200&t=b0b8785f45c38e1cf4c7c99fb8e40ec7",
        "https://img1.baidu.com/it/u=2342534061,3688025729&fm=253&fmt=auto&app=138&f=JPEG?w=200&h=200",
        "https://img1.baidu.com/it/u=3709586903,1286591012&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1687453200&t=94eb919c30f36e980c37d499a230d6b2"
      ]
    };
  },
  methods: {
    goComment(data) {
      this.$router.push({
        name: "comment",
        params: { id: this.postId }
      });
    },

    getNum() {
      getComments({
        params: {
          postId: this.postId
        }
      }).then(r => {
        console.log(
          "%c [r]: ",
          "color: #bf2c9f; background: pink; font-size: 13px;",
          r
        );
        this.cNum = r.length || 0;
        this.$store.commit("cnum/UP_CNUM", this.cNum);
      });
    }
  },
  mounted() {
    this.goodData = JSON.parse(this.$route.params.post.data);
    this.introduction = this.$route.params.post.post;
    this.postId = this.$route.params.post.id;
    this.username = this.$route.params.user.username;
    this.$nextTick(r => {
      this.getNum();
    });
  },
  computed: {
    ...mapState({
      cnum: state => state.cnum.cnum
    }),
    imgIndex() {
      function getRandomInt(min, max) {
        min = Math.ceil(min); // 向上取整，确保最小值是整数
        max = Math.floor(max); // 向下取整，确保最大值是整数
        return Math.floor(Math.random() * (max - min + 1)) + min;
      }
      let a = getRandomInt(0, 6);
      console.log(
        "%c [a]: ",
        "color: #bf2c9f; background: pink; font-size: 13px;",
        a
      );
      return a;
    }
  },
  components: {
    "v-header": header
  }
};
</script>

<style lang="less" scoped>
.posbox {
  position: relative;
  top: 1.5rem;
}
.user {
  background: #fff;
  padding: 10px;
  display: flex;
  .img {
    width: 80px;
    flex-shrink: 0;
  }
  .txt {
    height: 100%;
    font-size: 0.45rem;
    display: flex;
    flex-direction: column;
    text-indent: 0.3rem;
    margin-left: 10px;
  }
  .info {
    margin-top: 10px;
    color: #888;
    font-weight: 500;
    word-break: break-all;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.goodDetaiSwipe {
  height: 8rem;
  margin-top: 2px;
  background: white;
}
.goodDetaiSwipe img {
  width: 70%;
  height: 7rem;
  display: block;
  margin: auto;
}

.gooDetailNumber {
  display: none;
}
.goodDetailImg {
  margin-top: 4px;
  margin-bottom: 6px;
}
.goodDetailImg img {
  width: 100%;
  height: auto;
  display: block;
}
.footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  height: 1.45rem;
  position: fixed;
  bottom: 0;
  font-size: 14px;
  background: #fff;
  .num {
  }
  .btn {
    padding: 10px 20px;
    color: #fff;
    background: #26a2ff;
    border-radius: 30px;
  }
}
</style>



